import { FolderOutlined, FileOutlined } from "@ant-design/icons";
import { Col, Row } from "antd";
import { Button, Space, Modal } from "antd";
import React from "react";
import { Spin } from "antd";

function randomString(length) {
  let result = "";
  const characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  const charactersLength = characters.length;
  let counter = 0;
  while (counter < length) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
    counter += 1;
  }
  return result;
}

const sleep = (timeout) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve();
    }, timeout);
  });
};

async function listFiles(path) {
  await sleep(300 + Math.floor(Math.random() * 300));
  return [
    {
      type: "file",
      name: randomString(10) + ".txt",
      path: randomString(10),
      size: 100,
      time: "2023/05/20 18:09:00"
    },
    {
      type: "file",
      name: randomString(30) + ".txt",
      path: randomString(30),
      size: 100,
      time: "2023/05/20 18:09:00"
    },
    {
      type: "folder",
      name: randomString(10),
      size: 100,
      time: "2023/05/20 18:09:00"
    },
    {
      type: "folder",
      name: randomString(30),
      size: 100,
      time: "2023/05/20 18:09:01"
    }
  ];
}

async function getFile(path) {
  console.log("getpath", path);
  await sleep(300 + Math.floor(Math.random() * 1000));
  return path + path;
}

